import { Component, OnInit, OnDestroy, NgZone, inject } from '@angular/core';
import { environment } from '../../../../environments/environment';
import { SafeResourceUrl, DomSanitizer } from '@angular/platform-browser';
import { BroadcastMessageService } from '../../../core/services/message.service';
import { AuthTokenManagerService } from '../../../core/services/auth-token-manager.service';
import { Subscription, timer } from 'rxjs';

@Component({
  selector: 'app-flutter-layout',
  standalone: true,
  imports: [],
  templateUrl: './flutter-layout.component.html',
  styleUrl: './flutter-layout.component.scss',
})
export class FlutterLayoutComponent implements OnInit, OnDestroy {
  private sanitizer = inject(DomSanitizer);
  private messageService = inject(BroadcastMessageService);
  private authTokenManager = inject(AuthTokenManagerService);
  private ngZone = inject(NgZone);

  safeUrl: SafeResourceUrl;
  private flutterLoaded = false;
  private subscription = new Subscription();
  private initializationAttempts = 0;
  private readonly MAX_INITIALIZATION_ATTEMPTS = 5;
  private readonly RETRY_DELAY = 2000; // 2 seconds

  constructor() {
    this.safeUrl = this.sanitizer.bypassSecurityTrustResourceUrl(
      environment.flutterUrl,
    );
  }

  ngOnInit(): void {
    // Listen for iframe load event
    window.addEventListener('message', this.handleIframeLoad.bind(this));

    // Listen for connection status changes
    this.subscription.add(
      this.messageService.connectionStatus$.subscribe((status) => {
        if (status.isConnected && !this.flutterLoaded) {
          this.flutterLoaded = true;
          this.initializationAttempts = 0;
          // Send auth tokens to Flutter upon connection
          this.ngZone.runOutsideAngular(() => {
            setTimeout(() => {
              this.ngZone.run(() => {
                this.messageService.sendAuthTokensToFlutter();
              });
            }, 500);
          });
        }
      }),
    );

    // Set up a safety fallback timer to ensure auth tokens are sent
    this.setupFallbackTimer();
  }

  ngOnDestroy(): void {
    window.removeEventListener('message', this.handleIframeLoad.bind(this));
    this.subscription.unsubscribe();
  }

  private handleIframeLoad(event: MessageEvent): void {
    // If the message is not from our domain, ignore it
    if (event.data === null || event.data === undefined) return;

    // Handle different message types
    switch (event.data.type) {
      case 'flutter-initialized':
        this.flutterLoaded = true;
        this.initializationAttempts = 0;

        // Send auth tokens to Flutter when it's initialized
        this.sendAuthTokensToFlutter();
        break;

      case 'flutter-request-tokens':
        // Send tokens immediately when requested
        this.sendAuthTokensToFlutter(0);
        break;

      case 'flutter-keep-alive':
        // Just log these messages at debug level
        console.debug('Received keep-alive from Flutter', event.data.timestamp);
        // If Flutter is sending keep-alives but we haven't marked it as loaded, do so now
        if (!this.flutterLoaded) {
          this.flutterLoaded = true;
          this.initializationAttempts = 0;
          this.sendAuthTokensToFlutter();
        }
        break;
    }
  }

  private sendAuthTokensToFlutter(delayMs = 500): void {
    this.ngZone.runOutsideAngular(() => {
      setTimeout(() => {
        this.ngZone.run(() => {
          this.messageService.sendAuthTokensToFlutter();
        });
      }, delayMs);
    });
  }

  private setupFallbackTimer(): void {
    // Set up a repeating timer that attempts to send auth tokens if Flutter hasn't loaded
    const fallbackTimer = timer(5000, this.RETRY_DELAY).subscribe(() => {
      if (this.initializationAttempts >= this.MAX_INITIALIZATION_ATTEMPTS) {
        fallbackTimer.unsubscribe();
        return;
      }

      if (!this.flutterLoaded) {
        this.messageService.sendAuthTokensToFlutter();
        this.initializationAttempts++;
      } else {
        fallbackTimer.unsubscribe();
      }
    });

    this.subscription.add(fallbackTimer);
  }
}
